import React, { useState, useEffect, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { ArrowLeft, Clock, Info } from 'lucide-react';
import { useNavigate } from 'react-router-dom';

const WayraLevel = () => {
  const navigate = useNavigate();
  const gameAreaRef = useRef(null);
  const audioContextRef = useRef(null);
  
  const [gameState, setGameState] = useState('tutorial');
  const [windConsciousness, setWindConsciousness] = useState(0);
  const [timeLeft, setTimeLeft] = useState(120);
  const [score, setScore] = useState(0);
  const [wayraPosition, setWayraPosition] = useState({ x: 50, y: 80 });
  const [fallingObjects, setFallingObjects] = useState([]);
  const [particles, setParticles] = useState([]);
  const [currentTip, setCurrentTip] = useState(0);
  const [showLegend, setShowLegend] = useState(false);
  const [floatingMessage, setFloatingMessage] = useState(null);
  const [consecutiveHits, setConsecutiveHits] = useState(0);
  const [backgroundBrightness, setBackgroundBrightness] = useState(1);
  const [victoryEffects, setVictoryEffects] = useState(false);
  const [windParticles, setWindParticles] = useState([]);
  
  // Estados para microinteracciones de la barra
  const [barLuminousWave, setBarLuminousWave] = useState(false);
  const [barShake, setBarShake] = useState(false);
  const [barWindParticles, setBarWindParticles] = useState([]);
  
  // Estados para controles "Brisa Fresca"
  const [leftButtonPressed, setLeftButtonPressed] = useState(false);
  const [rightButtonPressed, setRightButtonPressed] = useState(false);
  const [leftButtonRipple, setLeftButtonRipple] = useState(false);
  const [rightButtonRipple, setRightButtonRipple] = useState(false);
  
  // Estados para efectos del Pulso del Viento
  const [timerTick, setTimerTick] = useState(false);
  const [timerThresholdFlash, setTimerThresholdFlash] = useState(false);
  const [timerPulse, setTimerPulse] = useState(false);
  const [timerShake, setTimerShake] = useState(false);
  const [previousTimeLeft, setPreviousTimeLeft] = useState(120);
  const [supportPhrase, setSupportPhrase] = useState('');
  const [timerCollapsed, setTimerCollapsed] = useState(false);

  // Estados para efectos ambientales progresivos
  const [skyIntensity, setSkyIntensity] = useState(0.3);
  const [windLinesVisible, setWindLinesVisible] = useState(false);
  const [turbinesSpeed, setTurbinesSpeed] = useState(1);
  const [rainbowVisible, setRainbowVisible] = useState(false);

  // Tipos de objetos que caen/flotan
  const objectTypes = {
    whirlwind: { emoji: '', name: 'Remolino azul', points: 8, type: 'positive', trajectory: 'spiral' },
    turbineBlade: { emoji: '', name: 'Aspa de turbina', points: 6, type: 'positive', trajectory: 'rotating' },
    windGust: { emoji: '', name: 'Rfaga de viento', points: 4, type: 'positive', trajectory: 'straight' },
    leaf: { emoji: '', name: 'Hojita verde', points: 3, type: 'positive', trajectory: 'dancing' },
    plasticBag: { emoji: '', name: 'Bolsa plstica', points: -5, type: 'negative', trajectory: 'floating' },
    paperTrash: { emoji: '', name: 'Basura de papel', points: -3, type: 'negative', trajectory: 'zigzag' },
    smokeCloud: { emoji: '', name: 'Nube de humo', points: -8, type: 'negative', trajectory: 'straight' },
    looseCable: { emoji: '', name: 'Cable suelto', points: -6, type: 'negative', trajectory: 'vibrating' }
  };

  // Consejos educativos sobre energa elica
  const windTips = [
    "El viento no se ve, pero siempre deja huella.",
    "Las zonas abiertas y altas capturan mejor la energa elica.",
    "Reducir la basura ligera evita que el viento la disperse.",
    "El viento es gratuito y no contamina.",
    "Ubicar turbinas en zonas abiertas mejora el rendimiento.",
    "La energa elica puede generar electricidad las 24 horas.",
    "Los aerogeneradores modernos son silenciosos y eficientes.",
    "El viento mueve turbinas... y tambin acciones."
  ];

  // Frases de apoyo para el temporizador
  const supportPhrases = [
    "El viento est contigo!",
    "Siente la brisa!",
    "Los vientos te favorecen!",
    "Atrapa las corrientes!",
    "El aire es tu aliado!",
    "Vuela con el viento!"
  ];

  // Funcin para obtener el degradado del cielo segn el progreso
  const getSkyGradient = () => {
    const progress = windConsciousness / 100;
    
    if (progress < 0.3) {
      // 0-30%: Azul plido
      return 'linear-gradient(to bottom, #A7F3D0 0%, #7DD3FC 50%, #3B82F6 100%)';
    } else if (progress < 0.7) {
      // 31-70%: Menta brillante
      return 'linear-gradient(to bottom, #10B981 0%, #22D3EE 50%, #3B82F6 100%)';
    } else if (progress < 1.0) {
      // 71-99%: Azul intenso
      return 'linear-gradient(to bottom, #059669 0%, #0891B2 50%, #1E40AF 100%)';
    } else {
      // 100%: Arcoris tenue
      return 'linear-gradient(to bottom, #F472B6 0%, #A78BFA 20%, #60A5FA 40%, #34D399 60%, #FBBF24 80%, #FB7185 100%)';
    }
  };

  // Funcin para obtener el cono dinmico de la barra
  const getWindIcon = () => {
    if (windConsciousness <= 30) return '';
    if (windConsciousness <= 70) return '';
    if (windConsciousness <= 99) return '';
    return '';
  };

  // Funcin para crear partculas de viento
  const createWindParticles = (x, y, type = 'positive') => {
    const newParticles = Array.from({ length: type === 'positive' ? 8 : 4 }, (_, i) => ({
      id: Date.now() + i,
      x: x + (Math.random() - 0.5) * 40,
      y: y + (Math.random() - 0.5) * 40,
      vx: (Math.random() - 0.5) * 4,
      vy: Math.random() * -3 - 1,
      life: 1,
      color: type === 'positive' ? '#10B981' : '#6B7280',
      size: Math.random() * 4 + 2
    }));
    
    setParticles(prev => [...prev, ...newParticles]);
  };

  // Funcin para reproducir sonidos
  const playSound = (type) => {
    if (!audioContextRef.current) {
      audioContextRef.current = new (window.AudioContext || window.webkitAudioContext)();
    }

    const ctx = audioContextRef.current;
    const oscillator = ctx.createOscillator();
    const gainNode = ctx.createGain();
    
    oscillator.connect(gainNode);
    gainNode.connect(ctx.destination);

    if (type === 'positive') {
      // Sonido "pim-air" cristalino
      oscillator.frequency.setValueAtTime(800, ctx.currentTime);
      oscillator.frequency.exponentialRampToValueAtTime(1200, ctx.currentTime + 0.2);
      oscillator.type = 'sine';
      gainNode.gain.setValueAtTime(0.1, ctx.currentTime);
      gainNode.gain.exponentialRampToValueAtTime(0.001, ctx.currentTime + 0.3);
      oscillator.start();
      oscillator.stop(ctx.currentTime + 0.3);
    } else if (type === 'negative') {
      // Sonido "bzzz" o "flup" opaco
      oscillator.frequency.setValueAtTime(150, ctx.currentTime);
      oscillator.type = 'sawtooth';
      gainNode.gain.setValueAtTime(0.05, ctx.currentTime);
      gainNode.gain.exponentialRampToValueAtTime(0.001, ctx.currentTime + 0.4);
      oscillator.start();
      oscillator.stop(ctx.currentTime + 0.4);
    } else if (type === 'combo') {
      // Sonido "woosh-combo" alegre
      oscillator.frequency.setValueAtTime(400, ctx.currentTime);
      oscillator.frequency.exponentialRampToValueAtTime(800, ctx.currentTime + 0.5);
      oscillator.type = 'triangle';
      gainNode.gain.setValueAtTime(0.15, ctx.currentTime);
      gainNode.gain.exponentialRampToValueAtTime(0.001, ctx.currentTime + 0.6);
      oscillator.start();
      oscillator.stop(ctx.currentTime + 0.6);
    }
  };

  // Funcin para verificar colisiones
  const checkCollisions = () => {
    const wayraRect = {
      x: wayraPosition.x - 3,
      y: wayraPosition.y - 3,
      width: 6,
      height: 6
    };

    setFallingObjects(prev => prev.filter(obj => {
      const objRect = {
        x: obj.x - 2,
        y: obj.y - 2,
        width: 4,
        height: 4
      };

      if (wayraRect.x < objRect.x + objRect.width &&
          wayraRect.x + wayraRect.width > objRect.x &&
          wayraRect.y < objRect.y + objRect.height &&
          wayraRect.y + wayraRect.height > objRect.y) {
        
        const objType = objectTypes[obj.type];
        const consciousnessChange = objType.points;
        
        setWindConsciousness(prev => Math.max(0, Math.min(100, prev + consciousnessChange)));
        setScore(prev => prev + Math.abs(consciousnessChange) * 10);
        
        // Crear partculas en el punto de colisin
        createWindParticles(obj.x, obj.y, objType.type);
        
        // Efectos visuales y sonoros
        if (objType.type === 'positive') {
          playSound('positive');
          setConsecutiveHits(prev => prev + 1);
          
          // Efectos de la barra
          setBarLuminousWave(true);
          setTimeout(() => setBarLuminousWave(false), 800);
          
          // Crear partculas en la barra
          const newBarParticles = Array.from({ length: 6 }, (_, i) => ({
            id: Date.now() + i,
            x: Math.random() * 100,
            delay: i * 0.1
          }));
          setBarWindParticles(newBarParticles);
          setTimeout(() => setBarWindParticles([]), 1500);
          
        } else {
          playSound('negative');
          setConsecutiveHits(0);
          
          // Efecto de sacudida en la barra
          setBarShake(true);
          setTimeout(() => setBarShake(false), 300);
          
          // Reducir brillo del cielo temporalmente
          setBackgroundBrightness(0.7);
          setTimeout(() => setBackgroundBrightness(1), 300);
        }
        
        // Mensaje flotante
        setFloatingMessage({
          text: `${consciousnessChange > 0 ? '+' : ''}${consciousnessChange}%`,
          x: obj.x,
          y: obj.y,
          color: objType.type === 'positive' ? '#10B981' : '#EF4444',
          id: Date.now()
        });
        
        // Combo de racha
        if (consecutiveHits >= 2 && objType.type === 'positive') {
          playSound('combo');
          setFloatingMessage(prev => ({
            ...prev,
            text: `Combo x${consecutiveHits + 1}! +${consciousnessChange}%`,
            color: '#22D3EE'
          }));
        }
        
        return false; // Remover objeto
      }
      return true; // Mantener objeto
    }));
  };

  // Funcin para generar objetos que caen
  const spawnObject = () => {
    if (gameState !== 'playing') return;
    
    const objectTypeKeys = Object.keys(objectTypes);
    const randomType = objectTypeKeys[Math.floor(Math.random() * objectTypeKeys.length)];
    const objType = objectTypes[randomType];
    
    // Ajustar probabilidades segn dificultad progresiva
    const timeElapsed = 120 - timeLeft;
    const difficultyMultiplier = 1 + (timeElapsed / 120) * 0.5;
    
    const newObject = {
      id: Date.now() + Math.random(),
      type: randomType,
      x: Math.random() * 90 + 5,
      y: -5,
      speed: (Math.random() * 2 + 1) * difficultyMultiplier,
      trajectory: objType.trajectory,
      rotation: 0,
      oscillation: Math.random() * Math.PI * 2
    };
    
    setFallingObjects(prev => [...prev, newObject]);
  };

  // Funcin para mover objetos
  const updateObjects = () => {
    setFallingObjects(prev => prev.map(obj => {
      let newX = obj.x;
      let newY = obj.y + obj.speed;
      let newRotation = obj.rotation;
      let newOscillation = obj.oscillation + 0.1;
      
      // Aplicar trayectorias especficas
      switch (obj.trajectory) {
        case 'spiral':
          newX += Math.sin(newOscillation) * 2;
          newRotation += 5;
          break;
        case 'rotating':
          newRotation += 3;
          break;
        case 'dancing':
          newX += Math.sin(newOscillation * 2) * 1.5;
          break;
        case 'floating':
          newY += Math.sin(newOscillation) * 0.5;
          newX += Math.cos(newOscillation * 0.5) * 1;
          break;
        case 'zigzag':
          newX += Math.sin(newOscillation * 3) * 2;
          break;
        case 'vibrating':
          newX += (Math.random() - 0.5) * 1;
          break;
        default:
          break;
      }
      
      return {
        ...obj,
        x: newX,
        y: newY,
        rotation: newRotation,
        oscillation: newOscillation
      };
    }).filter(obj => obj.y < 105)); // Remover objetos que salen de pantalla
  };

  // Funcin para actualizar partculas
  const updateParticles = () => {
    setParticles(prev => prev.map(particle => ({
      ...particle,
      x: particle.x + particle.vx,
      y: particle.y + particle.vy,
      life: particle.life - 0.02,
      vy: particle.vy + 0.1 // Gravedad
    })).filter(particle => particle.life > 0));
  };

  // Funcin para manejar movimiento de Wayra
  const handleButtonPress = (direction) => {
    if (gameState !== 'playing') return;
    
    setWayraPosition(prev => {
      const newX = direction === 'left' 
        ? Math.max(5, prev.x - 8) 
        : Math.min(95, prev.x + 8);
      return { ...prev, x: newX };
    });
    
    // Efectos visuales del botn
    if (direction === 'left') {
      setLeftButtonPressed(true);
      setLeftButtonRipple(true);
      setTimeout(() => setLeftButtonPressed(false), 150);
      setTimeout(() => setLeftButtonRipple(false), 600);
    } else {
      setRightButtonPressed(true);
      setRightButtonRipple(true);
      setTimeout(() => setRightButtonPressed(false), 150);
      setTimeout(() => setRightButtonRipple(false), 600);
    }
  };

  // Efectos del temporizador
  const triggerTimerTick = () => {
    setTimerTick(true);
    setTimeout(() => setTimerTick(false), 200);
  };

  const triggerTimerPulse = () => {
    setTimerPulse(true);
    setTimeout(() => setTimerPulse(false), 600);
  };

  const triggerTimerShake = () => {
    setTimerShake(true);
    setTimeout(() => setTimerShake(false), 400);
  };

  // Funcin para iniciar el juego
  const startGame = () => {
    setGameState('playing');
    setTimeLeft(120);
    setWindConsciousness(0);
    setScore(0);
    setFallingObjects([]);
    setParticles([]);
    setFloatingMessage(null);
    setConsecutiveHits(0);
    setBackgroundBrightness(1);
    setVictoryEffects(false);
    setWindParticles([]);
    setSkyIntensity(0.3);
    setWindLinesVisible(false);
    setTurbinesSpeed(1);
    setRainbowVisible(false);
  };

  const resetGame = () => {
    setGameState('tutorial');
    setWindConsciousness(0);
    setScore(0);
    setTimeLeft(120);
    setFallingObjects([]);
    setParticles([]);
    setFloatingMessage(null);
    setConsecutiveHits(0);
    setBackgroundBrightness(1);
    setVictoryEffects(false);
    setWindParticles([]);
    setSkyIntensity(0.3);
    setWindLinesVisible(false);
    setTurbinesSpeed(1);
    setRainbowVisible(false);
  };

  // Efectos y temporizadores
  useEffect(() => {
    if (gameState === 'playing') {
      const timer = setInterval(() => {
        setTimeLeft(prev => {
          if (prev <= 1) {
            setGameState('gameOver');
            return 0;
          }
          
          // Efectos del temporizador
          if (prev !== previousTimeLeft) {
            triggerTimerTick();
            
            if (prev <= 10) {
              triggerTimerPulse();
              if (prev <= 5) {
                triggerTimerShake();
              }
            }
            
            // Actualizar frase de apoyo cada 15 segundos
            if (prev % 15 === 0) {
              setSupportPhrase(supportPhrases[Math.floor(Math.random() * supportPhrases.length)]);
            }
            
            setPreviousTimeLeft(prev);
          }
          
          return prev - 1;
        });
      }, 1000);
      
      return () => clearInterval(timer);
    }
  }, [gameState, previousTimeLeft]);

  // Efectos visuales progresivos
  useEffect(() => {
    const progress = windConsciousness / 100;
    setSkyIntensity(0.3 + progress * 0.7);
    setWindLinesVisible(progress > 0.3);
    setTurbinesSpeed(1 + progress * 2);
    setRainbowVisible(progress >= 1.0);
    
    if (windConsciousness >= 100) {
      setGameState('victory');
      setVictoryEffects(true);
      playSound('combo');
    }
  }, [windConsciousness]);

  // Generacin de objetos
  useEffect(() => {
    if (gameState === 'playing') {
      const spawnInterval = setInterval(() => {
        spawnObject();
      }, 2000 - (120 - timeLeft) * 10); // Aumenta frecuencia con el tiempo
      
      return () => clearInterval(spawnInterval);
    }
  }, [gameState, timeLeft]);

  // Actualizacin de fsica
  useEffect(() => {
    if (gameState === 'playing') {
      const gameLoop = setInterval(() => {
        updateObjects();
        updateParticles();
        checkCollisions();
      }, 50);
      
      return () => clearInterval(gameLoop);
    }
  }, [gameState, wayraPosition, fallingObjects]);

  // Rotacin de consejos
  useEffect(() => {
    const tipInterval = setInterval(() => {
      setCurrentTip(prev => (prev + 1) % windTips.length);
    }, 12000);
    
    return () => clearInterval(tipInterval);
  }, []);

  // Control por teclado
  useEffect(() => {
    const handleKeyPress = (e) => {
      if (gameState !== 'playing') return;
      
      if (e.key === 'ArrowLeft' || e.key === 'a' || e.key === 'A') {
        handleButtonPress('left');
      } else if (e.key === 'ArrowRight' || e.key === 'd' || e.key === 'D') {
        handleButtonPress('right');
      }
    };

    window.addEventListener('keydown', handleKeyPress);
    return () => window.removeEventListener('keydown', handleKeyPress);
  }, [gameState]);

  return (
    <div className="min-h-screen w-full overflow-hidden relative" ref={gameAreaRef}>
      {/* Fondo del cielo con degradado dinmico */}
      <motion.div
        className="absolute inset-0"
        style={{
          background: getSkyGradient(),
          filter: `brightness(${backgroundBrightness})`,
        }}
        animate={{
          backgroundPosition: ['0% 0%', '100% 100%', '0% 0%'],
        }}
        transition={{
          duration: 20,
          repeat: Infinity,
          ease: "linear"
        }}
      />

      {/* Lneas de viento ondulantes */}
      <AnimatePresence>
        {windLinesVisible && (
          <div className="absolute inset-0 pointer-events-none">
            {Array.from({ length: 8 }).map((_, i) => (
              <motion.div
                key={`wind-line-${i}`}
                className="absolute h-0.5 bg-white/20 rounded-full"
                style={{
                  left: `${i * 15}%`,
                  top: `${20 + i * 10}%`,
                  width: '200px',
                  transform: 'rotate(-15deg)',
                }}
                initial={{ opacity: 0, scaleX: 0 }}
                animate={{ 
                  opacity: [0.2, 0.6, 0.2],
                  scaleX: [0.5, 1.5, 0.5],
                  x: [0, 100, 0]
                }}
                exit={{ opacity: 0 }}
                transition={{
                  duration: 4,
                  repeat: Infinity,
                  delay: i * 0.5,
                  ease: "easeInOut"
                }}
              />
            ))}
          </div>
        )}
      </AnimatePresence>

      {/* Montaas andinas fras al fondo */}
      <div className="absolute bottom-0 left-0 right-0 h-64 pointer-events-none">
        <svg viewBox="0 0 1200 300" className="w-full h-full">
          <defs>
            <linearGradient id="coldMountainGradient" x1="0%" y1="0%" x2="0%" y2="100%">
              <stop offset="0%" stopColor="#E0E7FF" />
              <stop offset="50%" stopColor="#C7D2FE" />
              <stop offset="100%" stopColor="#A5B4FC" />
            </linearGradient>
          </defs>
          <path
            d="M0,300 L0,180 L200,120 L400,140 L600,100 L800,130 L1000,110 L1200,125 L1200,300 Z"
            fill="url(#coldMountainGradient)"
            opacity="0.6"
          />
          <path
            d="M0,300 L0,220 L150,180 L350,200 L550,160 L750,190 L950,170 L1200,180 L1200,300 Z"
            fill="url(#coldMountainGradient)"
            opacity="0.4"
          />
        </svg>
      </div>

      {/* Nubes ligeras con parallax */}
      <div className="absolute inset-0 pointer-events-none">
        {Array.from({ length: 6 }).map((_, i) => (
          <motion.div
            key={`cloud-${i}`}
            className="absolute text-6xl opacity-30"
            style={{
              left: `${i * 20 - 10}%`,
              top: `${15 + i * 8}%`,
            }}
            animate={{
              x: [0, 50, 0],
              opacity: [0.2, 0.4, 0.2]
            }}
            transition={{
              duration: 15 + i * 3,
              repeat: Infinity,
              ease: "easeInOut",
              delay: i * 2
            }}
          >
            
          </motion.div>
        ))}
      </div>

      {/* Pradera verde esmeralda */}
      <div className="absolute bottom-0 left-0 right-0 h-32 pointer-events-none">
        <div 
          className="w-full h-full"
          style={{
            background: 'linear-gradient(to top, #047857 0%, #059669 50%, #10B981 100%)'
          }}
        />
        
        {/* Molinillos y aerogeneradores */}
        {Array.from({ length: 5 }).map((_, i) => (
          <motion.div
            key={`turbine-${i}`}
            className="absolute text-4xl"
            style={{
              left: `${15 + i * 15}%`,
              bottom: '20px',
            }}
            animate={{
              rotate: [0, 360]
            }}
            transition={{
              duration: 3 / turbinesSpeed,
              repeat: Infinity,
              ease: "linear"
            }}
          >
            
          </motion.div>
        ))}
      </div>

      {/* Partculas de hojitas y brillos en espiral */}
      <div className="absolute inset-0 pointer-events-none">
        {Array.from({ length: 12 }).map((_, i) => (
          <motion.div
            key={`leaf-particle-${i}`}
            className="absolute text-lg opacity-60"
            style={{
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
            }}
            animate={{
              x: [0, Math.sin(i) * 50, 0],
              y: [0, Math.cos(i) * 30, 0],
              rotate: [0, 360],
              opacity: [0.3, 0.8, 0.3]
            }}
            transition={{
              duration: 8 + Math.random() * 4,
              repeat: Infinity,
              delay: Math.random() * 2,
              ease: "easeInOut"
            }}
          >
            {i % 3 === 0 ? '' : i % 3 === 1 ? '' : ''}
          </motion.div>
        ))}
      </div>

      {/* Arcoris tenue al 100% */}
      <AnimatePresence>
        {rainbowVisible && (
          <motion.div
            className="absolute top-20 left-1/2 transform -translate-x-1/2 pointer-events-none"
            initial={{ opacity: 0, scale: 0.5 }}
            animate={{ opacity: 0.6, scale: 1 }}
            exit={{ opacity: 0, scale: 0.5 }}
            transition={{ duration: 2 }}
          >
            <div 
              className="w-96 h-48 rounded-full"
              style={{
                background: 'conic-gradient(from 0deg, transparent 0%, #F472B6 10%, #A78BFA 20%, #60A5FA 30%, #34D399 40%, #FBBF24 50%, #FB7185 60%, transparent 70%)',
                filter: 'blur(2px)'
              }}
            />
          </motion.div>
        )}
      </AnimatePresence>

      {/* HUD Superior */}
      {gameState === 'playing' && (
        <div className="absolute top-0 left-0 right-0 z-30 p-6">
          {/* Botn Volver */}
          <motion.button
            onClick={() => navigate('/')}
            className="absolute top-6 left-6 flex items-center gap-2 px-4 py-2 bg-white/10 backdrop-blur-md border border-white/20 rounded-lg text-white hover:bg-white/20 transition-all duration-300"
            style={{
              boxShadow: '0 0 20px rgba(16, 185, 129, 0.3)'
            }}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <ArrowLeft size={20} />
            <span className="font-inter">Volver</span>
          </motion.button>

          {/* Barra de Conciencia Elica */}
          <div className="flex justify-center">
            <motion.div
              className={`relative bg-white/10 backdrop-blur-md border border-white/20 rounded-full px-6 py-3 ${barShake ? 'animate-pulse' : ''}`}
              style={{
                boxShadow: barLuminousWave 
                  ? '0 0 30px rgba(16, 185, 129, 0.8), 0 0 60px rgba(34, 211, 238, 0.6)' 
                  : '0 0 20px rgba(16, 185, 129, 0.3)'
              }}
              animate={barLuminousWave ? {
                boxShadow: [
                  '0 0 20px rgba(16, 185, 129, 0.3)',
                  '0 0 40px rgba(16, 185, 129, 0.8), 0 0 80px rgba(34, 211, 238, 0.6)',
                  '0 0 20px rgba(16, 185, 129, 0.3)'
                ]
              } : undefined}
              transition={{ duration: 0.8 }}
            >
              <div className="flex items-center gap-4">
                <span className="text-2xl">{getWindIcon()}</span>
                <div className="flex flex-col items-center">
                  <span className="text-white font-bold text-lg">Conciencia Elica: {Math.round(windConsciousness)}%</span>
                  <div className="w-64 h-3 bg-gray-700/50 rounded-full overflow-hidden mt-1">
                    <motion.div
                      className="h-full rounded-full"
                      style={{
                        background: 'linear-gradient(90deg, #10B981, #22D3EE, #3B82F6)',
                        width: `${windConsciousness}%`
                      }}
                      animate={{
                        backgroundPosition: ['0% 0%', '100% 0%', '0% 0%']
                      }}
                      transition={{
                        duration: 2,
                        repeat: Infinity,
                        ease: "linear"
                      }}
                    />
                  </div>
                </div>
              </div>

              {/* Partculas de la barra */}
              {barWindParticles.map(particle => (
                <motion.div
                  key={particle.id}
                  className="absolute w-2 h-2 bg-white rounded-full pointer-events-none"
                  style={{
                    left: `${particle.x}%`,
                    top: '50%'
                  }}
                  initial={{ opacity: 0, scale: 0, y: 0 }}
                  animate={{ 
                    opacity: [0, 1, 0], 
                    scale: [0, 1, 0], 
                    y: [-20, -40, -60] 
                  }}
                  transition={{ 
                    duration: 1.5, 
                    delay: particle.delay,
                    ease: "easeOut" 
                  }}
                />
              ))}
            </motion.div>
          </div>

          {/* Temporizador Pulso del Viento */}
          <div className="absolute top-6 right-6">
            <motion.div
              className={`relative bg-white/10 backdrop-blur-md border border-white/20 rounded-full p-4 ${timerShake ? 'animate-bounce' : ''}`}
              style={{
                boxShadow: timeLeft > 60 
                  ? '0 0 20px rgba(16, 185, 129, 0.5)' 
                  : timeLeft > 10 
                    ? '0 0 20px rgba(251, 146, 60, 0.5)' 
                    : '0 0 20px rgba(239, 68, 68, 0.8)'
              }}
              animate={timerPulse ? { scale: [1, 1.1, 1] } : undefined}
              transition={{ duration: 0.6 }}
            >
              <div className="flex items-center gap-2">
                <Clock size={24} className="text-white" />
                <div className="text-center">
                  <div className={`text-xl font-bold ${timeLeft > 60 ? 'text-green-400' : timeLeft > 10 ? 'text-amber-400' : 'text-red-400'}`}>
                    {Math.floor(timeLeft / 60)}:{(timeLeft % 60).toString().padStart(2, '0')}
                  </div>
                  <div className="text-xs text-white/70">Pulso del Viento</div>
                </div>
              </div>
              
              {supportPhrase && (
                <motion.div
                  className="absolute -bottom-8 left-1/2 transform -translate-x-1/2 bg-white/10 backdrop-blur-md rounded-lg px-3 py-1 text-xs text-white whitespace-nowrap"
                  initial={{ opacity: 0, y: -10 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -10 }}
                >
                  {supportPhrase}
                </motion.div>
              )}
            </motion.div>
          </div>
        </div>
      )}

      {/* Wayra (personaje principal) */}
      {gameState === 'playing' && (
        <motion.div
          className="absolute text-6xl z-20 pointer-events-none"
          style={{
            left: `${wayraPosition.x}%`,
            top: `${wayraPosition.y}%`,
            transform: 'translate(-50%, -50%)'
          }}
          animate={{
            y: [0, -10, 0],
            rotate: [0, 5, -5, 0]
          }}
          transition={{
            duration: 2,
            repeat: Infinity,
            ease: "easeInOut"
          }}
        >
          
        </motion.div>
      )}

      {/* Objetos que caen/flotan */}
      {gameState === 'playing' && fallingObjects.map(obj => (
        <motion.div
          key={obj.id}
          className="absolute text-4xl z-15 pointer-events-none"
          style={{
            left: `${obj.x}%`,
            top: `${obj.y}%`,
            transform: `translate(-50%, -50%) rotate(${obj.rotation}deg)`
          }}
        >
          {objectTypes[obj.type].emoji}
        </motion.div>
      ))}

      {/* Partculas de efectos */}
      {particles.map(particle => (
        <motion.div
          key={particle.id}
          className="absolute rounded-full z-10 pointer-events-none"
          style={{
            left: `${particle.x}%`,
            top: `${particle.y}%`,
            width: `${particle.size}px`,
            height: `${particle.size}px`,
            backgroundColor: particle.color,
            opacity: particle.life
          }}
        />
      ))}

      {/* Mensaje flotante */}
      <AnimatePresence>
        {floatingMessage && (
          <motion.div
            className="absolute z-25 text-2xl font-bold pointer-events-none"
            style={{
              left: `${floatingMessage.x}%`,
              top: `${floatingMessage.y}%`,
              color: floatingMessage.color,
              textShadow: '2px 2px 4px rgba(0,0,0,0.5)'
            }}
            initial={{ opacity: 0, scale: 0.5, y: 0 }}
            animate={{ opacity: 1, scale: 1, y: -50 }}
            exit={{ opacity: 0, scale: 0.5, y: -100 }}
            transition={{ duration: 1.5 }}
          >
            {floatingMessage.text}
          </motion.div>
        )}
      </AnimatePresence>

      {/* Tutorial inicial */}
      <AnimatePresence>
        {gameState === 'tutorial' && (
          <motion.div
            className="absolute inset-0 z-40 flex items-center justify-center backdrop-blur-sm"
            style={{ background: 'radial-gradient(ellipse at center, rgba(16, 185, 129, 0.2) 0%, rgba(0, 0, 0, 0.3) 100%)' }}
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <motion.div
              className="bg-white/10 backdrop-blur-md border-2 border-white/30 rounded-3xl p-8 max-w-lg mx-4 text-center"
              style={{
                boxShadow: '0 0 50px rgba(16, 185, 129, 0.4), 0 20px 40px rgba(0, 0, 0, 0.2)',
                background: 'linear-gradient(135deg, rgba(255, 255, 255, 0.12), rgba(255, 255, 255, 0.08))'
              }}
              initial={{ scale: 0.8, y: 50 }}
              animate={{ scale: 1, y: 0 }}
              exit={{ scale: 0.8, y: 50 }}
              transition={{ duration: 0.5, ease: "easeOut" }}
            >
              <motion.div
                className="text-6xl mb-4"
                animate={{
                  rotate: [0, 10, -10, 0],
                  scale: [1, 1.1, 1]
                }}
                transition={{
                  duration: 2,
                  repeat: Infinity,
                  ease: "easeInOut"
                }}
              >
                
              </motion.div>
              
              <h2 className="text-3xl font-bold text-white mb-4">
                 Desafo del Viento!
              </h2>
              
              <div className="text-white/90 space-y-3 mb-6">
                <p>Atrapa remolinos y piezas elicas para generar energa limpia.</p>
                <p>Evita basura voladora, bolsas y humo.</p>
                <p>Completa la <strong>Conciencia Elica</strong> al 100% antes de que acabe el tiempo.</p>
                <p>Usa   o los botones para mover a Wayra.</p>
              </div>
              
              <motion.button
                onClick={startGame}
                className="group relative px-8 py-4 rounded-full font-bold text-lg text-white overflow-hidden"
                style={{
                  background: 'linear-gradient(90deg, #10B981, #22D3EE, #3B82F6)',
                  boxShadow: '0 0 30px rgba(16, 185, 129, 0.6), 0 8px 16px rgba(0, 0, 0, 0.2)',
                  border: '2px solid rgba(255, 255, 255, 0.3)'
                }}
                whileHover={{
                  scale: 1.05,
                  boxShadow: '0 0 50px rgba(16, 185, 129, 0.8), 0 12px 24px rgba(0, 0, 0, 0.3)'
                }}
                whileTap={{ scale: 0.95 }}
              >
                <span className="relative z-10"> Comenzar con el viento </span>
              </motion.button>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Mensajes educativos */}
      {gameState === 'playing' && (
        <div className="absolute top-32 right-6 z-30 max-w-xs">
          <motion.div
            className="bg-white/10 backdrop-blur-md border border-white/20 rounded-lg p-4"
            key={currentTip}
            initial={{ opacity: 0, x: 50 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: 50 }}
            transition={{ duration: 0.5 }}
          >
            <p className="text-white text-sm font-medium">{windTips[currentTip]}</p>
          </motion.div>
        </div>
      )}

      {/* Leyenda interactiva */}
      {gameState === 'playing' && (
        <div className="absolute bottom-6 left-6 z-30">
          <motion.button
            onClick={() => setShowLegend(!showLegend)}
            className="bg-white/10 backdrop-blur-md border border-white/20 rounded-full p-3 text-white hover:bg-white/20 transition-all duration-300"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <Info size={20} />
          </motion.button>

          <AnimatePresence>
            {showLegend && (
              <motion.div
                className="absolute bottom-16 left-0 bg-white/10 backdrop-blur-md border border-white/20 rounded-lg p-4 w-64"
                initial={{ opacity: 0, y: 20, scale: 0.9 }}
                animate={{ opacity: 1, y: 0, scale: 1 }}
                exit={{ opacity: 0, y: 20, scale: 0.9 }}
                transition={{ duration: 0.3 }}
              >
                <h3 className="text-white font-bold mb-3">Elementos del Juego</h3>
                <div className="space-y-2 text-sm">
                  {Object.entries(objectTypes).map(([key, obj]) => (
                    <div key={key} className="flex items-center gap-2">
                      <span className="text-lg">{obj.emoji}</span>
                      <span className="text-white/80">{obj.name}</span>
                      <span className={`ml-auto font-bold ${obj.type === 'positive' ? 'text-green-400' : 'text-red-400'}`}>
                        {obj.points > 0 ? '+' : ''}{obj.points}%
                      </span>
                    </div>
                  ))}
                </div>
              </motion.div>
            )}
          </AnimatePresence>
        </div>
      )}

      {/* Controles "Brisa Fresca" */}
      {gameState === 'playing' && (
        <div className="absolute bottom-6 left-1/2 transform -translate-x-1/2 z-30 flex gap-8">
          {/* Botn Izquierda */}
          <div className="relative">
            <motion.button
              onMouseDown={() => handleButtonPress('left')}
              onTouchStart={() => handleButtonPress('left')}
              className={`relative w-16 h-16 rounded-full border-2 border-white/30 backdrop-blur-md text-white text-2xl font-bold overflow-hidden ${leftButtonPressed ? 'scale-95' : ''}`}
              style={{
                background: leftButtonPressed 
                  ? 'linear-gradient(135deg, rgba(16, 185, 129, 0.4), rgba(34, 211, 238, 0.4))' 
                  : 'linear-gradient(135deg, rgba(255, 255, 255, 0.12), rgba(255, 255, 255, 0.08))',
                boxShadow: leftButtonPressed 
                  ? '0 0 30px rgba(16, 185, 129, 0.6), inset 0 4px 8px rgba(0, 0, 0, 0.2)' 
                  : '0 0 20px rgba(16, 185, 129, 0.3), 0 4px 12px rgba(0, 0, 0, 0.15)'
              }}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              
              
              {/* Efecto ripple */}
              <AnimatePresence>
                {leftButtonRipple && (
                  <motion.div
                    className="absolute inset-0 rounded-full border-2 border-white/50"
                    initial={{ scale: 0, opacity: 1 }}
                    animate={{ scale: 2, opacity: 0 }}
                    exit={{ opacity: 0 }}
                    transition={{ duration: 0.6 }}
                  />
                )}
              </AnimatePresence>
              
              {/* Partculas al presionar */}
              {leftButtonPressed && Array.from({ length: 6 }).map((_, i) => (
                <motion.div
                  key={i}
                  className="absolute w-1 h-1 bg-white rounded-full"
                  style={{
                    left: '50%',
                    top: '50%'
                  }}
                  initial={{ scale: 0, x: 0, y: 0 }}
                  animate={{
                    scale: 1,
                    x: Math.cos(i * 60 * Math.PI / 180) * 30,
                    y: Math.sin(i * 60 * Math.PI / 180) * 30,
                    opacity: 0
                  }}
                  transition={{ duration: 0.6 }}
                />
              ))}
            </motion.button>
          </div>

          {/* Botn Derecha */}
          <div className="relative">
            <motion.button
              onMouseDown={() => handleButtonPress('right')}
              onTouchStart={() => handleButtonPress('right')}
              className={`relative w-16 h-16 rounded-full border-2 border-white/30 backdrop-blur-md text-white text-2xl font-bold overflow-hidden ${rightButtonPressed ? 'scale-95' : ''}`}
              style={{
                background: rightButtonPressed 
                  ? 'linear-gradient(135deg, rgba(16, 185, 129, 0.4), rgba(34, 211, 238, 0.4))' 
                  : 'linear-gradient(135deg, rgba(255, 255, 255, 0.12), rgba(255, 255, 255, 0.08))',
                boxShadow: rightButtonPressed 
                  ? '0 0 30px rgba(16, 185, 129, 0.6), inset 0 4px 8px rgba(0, 0, 0, 0.2)' 
                  : '0 0 20px rgba(16, 185, 129, 0.3), 0 4px 12px rgba(0, 0, 0, 0.15)'
              }}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              
              
              {/* Efecto ripple */}
              <AnimatePresence>
                {rightButtonRipple && (
                  <motion.div
                    className="absolute inset-0 rounded-full border-2 border-white/50"
                    initial={{ scale: 0, opacity: 1 }}
                    animate={{ scale: 2, opacity: 0 }}
                    exit={{ opacity: 0 }}
                    transition={{ duration: 0.6 }}
                  />
                )}
              </AnimatePresence>
              
              {/* Partculas al presionar */}
              {rightButtonPressed && Array.from({ length: 6 }).map((_, i) => (
                <motion.div
                  key={i}
                  className="absolute w-1 h-1 bg-white rounded-full"
                  style={{
                    left: '50%',
                    top: '50%'
                  }}
                  initial={{ scale: 0, x: 0, y: 0 }}
                  animate={{
                    scale: 1,
                    x: Math.cos(i * 60 * Math.PI / 180) * 30,
                    y: Math.sin(i * 60 * Math.PI / 180) * 30,
                    opacity: 0
                  }}
                  transition={{ duration: 0.6 }}
                />
              ))}
            </motion.button>
          </div>
        </div>
      )}

      {/* Pantalla de Game Over */}
      <AnimatePresence>
        {gameState === 'gameOver' && (
          <motion.div
            className="absolute inset-0 z-40 flex items-center justify-center backdrop-blur-sm"
            style={{ background: 'radial-gradient(ellipse at center, rgba(107, 114, 128, 0.3) 0%, rgba(0, 0, 0, 0.5) 100%)' }}
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <motion.div
              className="bg-white/10 backdrop-blur-md border-2 border-white/30 rounded-3xl p-8 max-w-md mx-4 text-center"
              initial={{ scale: 0.8, y: 50 }}
              animate={{ scale: 1, y: 0 }}
              exit={{ scale: 0.8, y: 50 }}
            >
              <div className="text-6xl mb-4"></div>
              <h2 className="text-3xl font-bold text-white mb-4">El viento se calm!</h2>
              <p className="text-white/80 mb-2">Conciencia Elica: {Math.round(windConsciousness)}%</p>
              <p className="text-white/80 mb-6">Puntuacin: {score}</p>
              
              <div className="flex gap-4 justify-center">
                <motion.button
                  onClick={resetGame}
                  className="px-6 py-3 bg-gradient-to-r from-teal-500 to-blue-500 rounded-lg text-white font-semibold"
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  Reintentar
                </motion.button>
                <motion.button
                  onClick={() => navigate('/')}
                  className="px-6 py-3 bg-white/20 backdrop-blur-md border border-white/30 rounded-lg text-white font-semibold"
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  Volver a los Niveles
                </motion.button>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Pantalla de Victoria */}
      <AnimatePresence>
        {gameState === 'victory' && (
          <motion.div
            className="absolute inset-0 z-40 flex items-center justify-center backdrop-blur-sm"
            style={{ background: 'radial-gradient(ellipse at center, rgba(16, 185, 129, 0.3) 0%, rgba(0, 0, 0, 0.3) 100%)' }}
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <motion.div
              className="bg-white/10 backdrop-blur-md border-2 border-white/30 rounded-3xl p-8 max-w-md mx-4 text-center"
              initial={{ scale: 0.8, y: 50 }}
              animate={{ scale: 1, y: 0 }}
              exit={{ scale: 0.8, y: 50 }}
            >
              <motion.div
                className="text-6xl mb-4"
                animate={{
                  rotate: [0, 360],
                  scale: [1, 1.2, 1]
                }}
                transition={{
                  duration: 2,
                  repeat: Infinity
                }}
              >
                
              </motion.div>
              
              <h2 className="text-3xl font-bold text-white mb-4">Wayra limpia el cielo contigo!</h2>
              <p className="text-xl text-teal-300 mb-4">"El viento mueve turbinas... y tambin acciones."</p>
              <p className="text-white/80 mb-2">Conciencia Elica: 100%</p>
              <p className="text-white/80 mb-6">Puntuacin Final: {score}</p>
              
              <div className="flex gap-4 justify-center">
                <motion.button
                  onClick={resetGame}
                  className="px-6 py-3 bg-gradient-to-r from-teal-500 to-blue-500 rounded-lg text-white font-semibold"
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  Reintentar
                </motion.button>
                <motion.button
                  onClick={() => navigate('/')}
                  className="px-6 py-3 bg-white/20 backdrop-blur-md border border-white/30 rounded-lg text-white font-semibold"
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  Volver a los Niveles
                </motion.button>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default WayraLevel;